"use client";

import React, { useState, useRef } from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Card } from "@/components/ui/card";

interface ChatInputProps {
  onSendMessage: (message: string, file: File | null) => Promise<void>;
  loading: boolean;
  error: string | null;
}

export default function ChatInput({ onSendMessage, loading, error }: ChatInputProps) {
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const [message, setMessage] = useState("");
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const fileInputRef = React.useRef<HTMLInputElement>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!message && !selectedFile) return;

    await onSendMessage(message, selectedFile);
    setMessage("");
    setSelectedFile(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      if (!file.type.startsWith("image/")) {
        alert("Only image files are allowed");
        return;
      }
      setSelectedFile(file);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}
      
      <Card className="p-4 bg-white shadow-sm border-t">
        <div className="flex gap-4 items-end">
          <div className="flex-1">
            <Textarea
              ref={textareaRef}
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              onKeyDown={handleKeyDown}
              placeholder="Type your message here... (Press Enter to send)"
              className="min-h-[60px] max-h-[200px] resize-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
          
          <div className="flex items-center gap-2">
            <input
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              ref={fileInputRef}
              className="hidden"
              id="image-upload"
            />
            <label
              htmlFor="image-upload"
              className={`flex items-center justify-center h-10 px-4 border rounded-md cursor-pointer transition-colors duration-200 ${
                selectedFile 
                  ? 'bg-green-50 text-green-600 border-green-200 hover:bg-green-100' 
                  : 'hover:bg-gray-50'
              }`}
            >
              {selectedFile ? (
                <span className="flex items-center gap-1">
                  <span className="text-sm">✓</span>
                  <span className="text-sm">Image</span>
                </span>
              ) : (
                <span className="flex items-center gap-1">
                  <span className="text-sm">📎</span>
                  <span className="text-sm">Attach</span>
                </span>
              )}
            </label>
            
            <Button 
              type="submit" 
              disabled={loading || (!message && !selectedFile)}
              className={`w-24 h-10 ${loading ? 'opacity-80' : ''}`}
              variant={loading ? "outline" : "default"}
            >
              {loading ? (
                <span className="flex items-center justify-center gap-1">
                  <span className="animate-pulse">...</span>
                </span>
              ) : (
                "Send"
              )}
            </Button>
          </div>
        </div>
      </Card>
    </form>
  );
}
