"use client";

import React from "react";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Card } from "@/components/ui/card";
import type { Message } from "@/hooks/useChat";

interface ChatHistoryProps {
  messages: Message[];
  loading?: boolean;
}

export default function ChatHistory({ messages, loading }: ChatHistoryProps) {
  const scrollRef = React.useRef<HTMLDivElement>(null);
  const [isAtBottom, setIsAtBottom] = React.useState(true);

  const scrollToBottom = React.useCallback(() => {
    if (scrollRef.current && isAtBottom) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [isAtBottom]);

  React.useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);

  const handleScroll = (e: React.UIEvent<HTMLDivElement>) => {
    const div = e.currentTarget;
    const isBottom = Math.abs(div.scrollHeight - div.scrollTop - div.clientHeight) < 50;
    setIsAtBottom(isBottom);
  };

  return (
    <ScrollArea className="h-[400px] w-full rounded-md border bg-white">
      <div 
        className="flex flex-col space-y-4 p-4" 
        ref={scrollRef}
        onScroll={handleScroll}
      >
        {messages.map((msg) => (
          <div
            key={msg.id}
            className={`flex ${msg.sender === "user" ? "justify-end" : "justify-start"}`}
          >
            <Card
              className={`max-w-[80%] p-3 ${
                msg.sender === "user"
                  ? "ml-auto bg-blue-500 text-white"
                  : "bg-gray-100"
              }`}
            >
              <div className="text-xs opacity-70 mb-1">
                {msg.timestamp}
              </div>
              {msg.type === "text" ? (
                <div className="whitespace-pre-wrap break-words">
                  {msg.content || (msg.sender === "chatgpt" && loading && (
                    <span className="inline-flex items-center gap-1">
                      <span className="w-1.5 h-1.5 bg-current rounded-full animate-bounce" />
                      <span className="w-1.5 h-1.5 bg-current rounded-full animate-bounce [animation-delay:0.2s]" />
                      <span className="w-1.5 h-1.5 bg-current rounded-full animate-bounce [animation-delay:0.4s]" />
                    </span>
                  ))}
                </div>
              ) : (
                <img
                  src={msg.content}
                  alt="User uploaded"
                  className="max-w-full rounded-md"
                  style={{ maxHeight: "300px" }}
                />
              )}
            </Card>
          </div>
        ))}
      </div>
    </ScrollArea>
  );
}
