"use client";

import React, { Suspense } from "react";
import ChatHistory from "@/components/ChatHistory";
import ChatInput from "@/components/ChatInput";
import useChat from "@/hooks/useChat";
import { Card } from "@/components/ui/card";

function ChatboxContent() {
  const { messages, loading, error, sendMessage } = useChat();

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-50 to-gray-100">
      <div className="container mx-auto px-4 py-8 max-w-4xl h-screen flex flex-col">
        <Card className="flex-1 shadow-2xl flex flex-col overflow-hidden">
          {/* Header */}
          <div className="p-6 border-b bg-gradient-to-r from-gray-900 to-gray-800 rounded-t-lg">
            <h1 className="text-2xl font-bold text-white">ChatGPT Window</h1>
            <p className="text-gray-300 mt-1 text-sm">Ask anything or share an image</p>
          </div>

          {/* Chat History */}
          <div className="flex-1 p-6 bg-white overflow-hidden">
            <ChatHistory messages={messages} loading={loading} />
          </div>

          {/* Input Area */}
          <div className="p-4 border-t bg-gray-50">
            <ChatInput
              onSendMessage={sendMessage}
              loading={loading}
              error={error}
            />
          </div>
        </Card>
      </div>
    </div>
  );
}

export default function ChatboxPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-xl text-gray-600">Loading...</div>
      </div>
    }>
      <ChatboxContent />
    </Suspense>
  );
}
